.parentClass {
    padding-top: 1rem;
    height: 100%;
}
.projects {
  /* padding-top: 1rem; */
  /* background-color: red; */
  display: flex;
  /* height: 200%; */
  flex-direction: column;
  justify-content: center;
  font-family: 'Roboto Slab', 'Cambria', 'Lucida Bright', monospace, sans-serif;

  @media (min-width: 640px) {
    /* Small screens and above (e.g., mobile devices in landscape) */
    padding-left: 2rem;
    padding-right: 2rem;
  }

  @media (min-width: 768px) {
    /* Medium screens and above (e.g., tablets) */
    padding-left: 4rem;
    padding-right: 4rem;
  }

  @media (min-width: 1024px) {
    /* Large screens and above (e.g., laptops and desktops) */
    padding-left: 7rem;
    padding-right: 7rem;
  }

  @media (min-width: 1280px) {
    /* Extra-large screens and above (e.g., large desktops) */
    padding-left: 10rem;
    padding-right: 10rem;
  }
  @media (min-width: 1780px) {
    /* Extra-large screens and above (e.g., large desktops) */
    padding-left: 26rem;
    padding-right: 26rem;
  }
}
.detail {
    font-size: 1.5rem;
    list-style-type: disc;
    margin-left: 20px;
}

.tech {
    margin: 0.2rem;
    padding: 0.2rem 0.4rem;
    font-size: 1.3rem;
    color: rgb(255, 255, 255);
    border-radius: 5%;
    background-color: rgb(62, 146, 236);
}